<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="12">
                <v-text-field
                        v-model="search"
                        label="Поиск"
                        prepend-inner-icon="mdi-magnify"
                        outlined
                        required
                ></v-text-field>
                <v-simple-table v-if="orders">
                    <thead>
                    <tr>
                        <th class="text-left">ID</th>
                        <th class="text-left">Пользователь</th>
                        <th class="text-left">План</th>
                        <th class="text-left">Остаток</th>
                        <th class="text-left">Кол-во</th>
                        <th class="text-left">Цена</th>
                        <th class="text-left">Дата</th>
                        <th class="text-left">Статус</th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="order in orders_filtered" :key="order.id">
                        <td>{{order.id}}</td>
                        <td>
                            <RouterLink :to="`/accounts/${order.user.id}`">{{order.user.email}}</RouterLink>
                        </td>
                        <td>
                            <RouterLink :to="`/plans/${order.plan.id}`">{{order.plan.name}}</RouterLink>
                        </td>
                        <td>{{order.remains}}</td>
                        <td>{{order.amount}}</td>
                        <td>{{order.price}}</td>
                        <td>{{new Date(order.date).toLocaleDateString()}}</td>
                        <td>{{order.status.name}}</td>
                        <td>
                            <RouterLink :to="`/orders/${order.id}`">
                                <v-icon>mdi-account-edit</v-icon>
                            </RouterLink>
                        </td>
                    </tr>
                    </tbody>
                </v-simple-table>
                <v-btn to="/orders/add" class="primary mt-5">Добавить заказ</v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name: "Orders",
        data: () => ({
            search: '',
            orders: null
        }),
        methods: {
            async getOrders() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/orders/`, method: 'GET'})
                this.orders = res.data
            }
        },
        mounted() {
            this.getOrders()
        },
        computed: {
            orders_filtered: function() {
                return this.search ? this.orders.filter(order => order.user.email.toLowerCase().includes(this.search.toLowerCase())) : this.orders
            }
        },
    }
</script>

<style scoped>
    a {
        text-decoration: none;
    }

    i {
        transition: all .3s;
        opacity: 0;
        cursor: pointer;
    }

    tr:hover i {
        opacity: 1;
    }
</style>